.enrollment {
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    padding: 20px;
}

.enrollment-title {
    flex: 1;
    text-align: center;
    font-size: 1.5rem;
    margin: 15rem 3rem 0 3rem;
}

.enrollment-btn-grp {
    flex: 1;
    display: flex;
    flex-direction: row;

}

.enrollment-btn {
    flex: 1;
    background-color: darkseagreen;
    padding: 1rem;
    margin: 1rem 4rem;
}

.buffer {
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    flex-direction: row;
    text-align: center;
    background-color: #e7e7e7;
    margin: 8rem;
    padding: 1.5rem;
    align-content: center;
    border-radius: 5px;
}

.buffer-item {
    flex-shrink: 1;
    display: inline;
    font-size: 2rem;
    color: white;
    background-color: chocolate;
    margin: 0.5rem;
    padding: 1rem;
    border-radius: 5px;
}

.buffer-item-btn {
    background-color: #cccccc;
}

label {
    padding: 12px 12px 12px 0;
    display: inline-block;
}


